<template>
  <div class="wrapper" ref="wrapper">
    <slot></slot>
  </div>
</template>

<script>
  import Bscroll from 'better-scroll';
  export default {
    props: {
      startY: {
        type: Number,
        default: 0
      },
      // 123swiper
      probeType: {
        type: Number,
        default: 1
      },
      // click
      click: {
        type: Boolean,
        default: true
      },
      // 
      scrollX: {
        type: Boolean,
        default: false
      },
      // 
      listenScroll: {
        type: Boolean,
        default: false
      },
      // 
      data: {
        type: Array,
        default: null
      },
      // 
      pullup: {
        type: Boolean,
        default: false
      },
      // 
      pullup: {
        type: Boolean,
        default: false
      },
      // 
      beforeScroll: {
        type: Boolean,
        default: false
      },
      // 
      afterScroll: {
        type: Boolean,
        default: false
      },
      // scroll
      refreshDelay: {
        type: Number,
        default: 20
      }
    },
    mounted() {
      this.$nextTick(() => {
        this._initScroll();
      })
    },
    methods: {
      _initScroll() {
        if (!this.$refs.wrapper) return;
        // 
        this.scroll = new Bscroll(this.$refs.wrapper, {
          startY: this.startY,
          probeType: this.probeType,
          click: this.click,
          scrollX: this.scrollX
        });
        // 
        if (this.listenScroll) {
          this.scroll.on('scroll', (pos) => {
            this.$emit('scroll', pos);
          });
        }
        // 
        if (this.pullup) {
          this.scroll.on('scrollEnd', () => {
            if (this.scroll.y <= (this.scroll.maxScrollY + 50)) {
              this.$emit('scrollToEnd');
            }
          });
        }
        // 
        if (this.pulldown) {
          this.scroll.on('scrollend', (pos) => {
            alert();
            if (pos.y > 50) {
              this.$emit('pulldown');
            }
          });
        }
        // 
        if (this.beforeScroll) {
          this.scroll.on('beforeScrollStart', () => {
            this.$emit('beforeScroll');
          });
        }
        // 
        if (this.afterScroll) {
          this.scroll.on('scrollEnd', (pos) => {
            this.$emit('scrollEnd', pos);
          });
        }
      },
      disable() {
        // better-scrolldisable
        this.scroll && this.scroll.disable();
      },
      enable() {
        // better-enableenable
        this.scroll && this.scroll.enable();
      },
      refresh() {
        // better-scrollrefresh
        this.scroll && this.scroll.refresh();
      },
      scrollTo() {
        // better-scrollscrollTo
        this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments);
      },
      scrollToElement() {
        // better-scrollscrollElement
        this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments);
      }
    },
    watch: {
      // refreshDelayrefresh
      data() {
        setTimeout(() => {
          this.refresh();
        }, this.refreshDelay);
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .wrapper{
    height: 100%;
    overflow: hidden;
  }
</style>



